@import url('https://fonts.googleapis.com/css2?family=Bonheur+Royale&family=Imperial+Script&family=Mrs+Saint+Delafield&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Imperial+Script&family=Mrs+Saint+Delafield&display=swap');



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html[data-theme="dark"] {
  background-color: black;
  color: white;
}

html[data-theme="light"] {
  background-color: white;
  color: black;
}
